/* titan-one-regular - latin-ext_latin */
@font-face {
  font-family: 'Titan One';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/titan-one-v8-latin-ext_latin-regular.eot'); /* IE9 Compat Modes */
  src: local(''),
    url('../fonts/titan-one-v8-latin-ext_latin-regular.eot?#iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */ url('../fonts/titan-one-v8-latin-ext_latin-regular.woff2')
      format('woff2'),
    /* Super Modern Browsers */
      url('../fonts/titan-one-v8-latin-ext_latin-regular.woff') format('woff'),
    /* Modern Browsers */
      url('../fonts/titan-one-v8-latin-ext_latin-regular.ttf')
      format('truetype'),
    /* Safari, Android, iOS */
      url('../fonts/titan-one-v8-latin-ext_latin-regular.svg#TitanOne')
      format('svg'); /* Legacy iOS */
}

